.wrapper {
  border: 2px solid gray;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding: 25px;
  width: 100%;
}

.link_to_card {
  color: black;
  text-decoration: none;
  width: 49%;
}

@media screen and (max-width: 1100px) {
  .link_to_card {
    width: 100%;
  }
}